 .Container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  
  padding: 0 15px 0 15px;
  box-sizing: border-box;
  // border: 1px solid red;

  @media(min-width: 768px){
    padding: 0 32px 0 32px; 
  }

  @media(min-width: 1024px){
    padding: 0 60px 0 60px;
  }

  @media(min-width: 1170px){
    padding: 0;
  }
}